

import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { MessageService, SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';

import { PageOrder } from '../../../../constants/page.constant';
import { ToastMessageService } from '../../../../services/toast/toast-message.service';
import { ToastEnum, ToastPrimeSeverityEnum } from '../../../../constants/toast.constant';
import { CondominioDto } from '../../../../interfaces/condominios/condominio.dto';
import { CondominioService } from '../../../../services/condominio/condominio.service';
import { EnderecoComponent } from '../../../endereco-module/endereco/endereco.component';
import { UpdateEnderecoComponent } from '../../../endereco-module/update-endereco/update-endereco.component';
import { PageableDto } from '../../../../interfaces/others/pageable.dto';
import { CondominioCadastroComponent } from '../condominios-cadastro/condominios-cadastro.component';
import { CondominioDetailComponent } from '../condominios-detail/condominios-detail.component';

@Component({
  selector: 'app-condominio-main',
  templateUrl: './condominios-main.component.html',
  styleUrls: ['./condominios-main.component.scss'],
})
export class CondominioMainComponent implements OnInit, OnDestroy {

  condominios: PageableDto<CondominioDto>;
  sortOptions: SelectItem[] = [];

  currentPage = 1;
  isLoaded = false;

  private subscriptions = new Subscription();

  constructor(
    private readonly condominioService: CondominioService,
    private readonly modal: ModalController,
    private readonly toastService: ToastMessageService,
  ) { }

  ngOnInit() {
    this.loadCondominios();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadCondominios(page: number = 1) {
    const sub = this.condominioService.list({}, { order: PageOrder.ASC, page, take: 5 })
      .subscribe(data => {
        this.condominios = data;
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  CondominioLabel(Condominio: CondominioDto) {
    return `${Condominio.nome} - ${Condominio.codigo}`;
  }

  search() {
    this.loadCondominios(1);
  }

  paginar(event) {
    const { first, rows } = event;
    const page = Number((Number(first) / Number(rows)) + 1);
    if (page != this.currentPage) {
      this.currentPage = page;
      this.loadCondominios(page);
    }
  }

  createNew() {
    // TODO Nova Condominio
    this.showModal(CondominioCadastroComponent);
  }

  editarCondominio(Condominio: CondominioDto) {
    this.showModal(CondominioDetailComponent, { Condominio });
  }


  editarEndereco(Condominio: CondominioDto) {
    if (Condominio.endereco) {
      this.showModal(UpdateEnderecoComponent, { currentEndereco: Condominio, isFuncionario: false });
    } else {
      this.showModal(EnderecoComponent, { idOf: Condominio.id, isFuncionario: false, isModal: true });
    }
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss()
      .then((data) => {
        const { data: hasUpdate } = data;
        if (hasUpdate) {
          this.loadCondominios();
          this.toastService.presentToast({
            detalhe: `Operação bem sucedida!`,
            titulo: `Sucesso!`,
            duracao: ToastEnum.mediumDuration,
            gravidade: ToastPrimeSeverityEnum.SUCESSO
          });
        }
      });

    return await modal.present();
  }

}
